import React from 'react';

import searchContent from '../../Common/Imgs/searchContent.png';
import InputsIcon from './InputsIcon';

import GetVoice from '../Getinputs/InputFields/GetVoice';
import GetImgInput from './InputFields/GetImgInput';
import GetVideoInput from './InputFields/GetVideoInput';

export default function GetInputFromUser(props) {

  const handle_input = (e) => {
    e.preventDefault();
  }

  return (

    <div className='inputcontainerBG pb-5 rounded h-100' >

      <div className="search_input_top_img pb-4  d-flex justify-content-center">
        <img className='img-fluid' id='input_page_relative_img' src={searchContent} alt="content Not Found" />
      </div>

      <div className="text_input_container  ">

        <form className='w-100 d-flex justify-content-center '>

          <input type="text" className='text_input px-3 py-2' placeholder='Search Planet or type a URL' />
          <button type='submit' onClick={handle_input} className='border border-2 border-info ' ><i className="fa-solid fa-magnifying-glass search_icon_color-gradient"></i></button>

        </form>

      </div>

      <div className="d-md-none">
        <InputsIcon />
      </div>

      <div className="row mt-5 g-2 mx-md-2 d-flex justify-content-center">

        <div className="col-md-4 mb-3">
          {props.image && <GetImgInput />}
        </div>

        <div className="col-md-4 mb-3">
         {props.audio && <GetVoice />}
        </div>

        <div className="col-md-4 mb-3">
         {props.video && <GetVideoInput />}
        </div>

      </div>

    </div>

  )
}
