import React, { useContext, useRef, useState } from 'react';

import { sendAlert } from '../../../App';

import Cropper from 'react-cropper';
import NatureImg from '../../../Common/Imgs/nature.jpg';

import 'cropperjs/dist/cropper.css';

export default function GetImgInput() {
    
    const alert = useContext(sendAlert);
    const [image, setImage] = useState(null);
    const [croppedImage, setCroppedImage] = useState(null);
    const inputRef = useRef(null);
    const cropperRef = useRef(null);
    const [allowUpload, setAllowUpload] = useState(true);
    const [showRecropBtn, setShowRecropBtn] = useState(false);

    const handleImgClick = () => {
        if (!image) {
            inputRef.current.click();
        }
    };

    const handleImgChange = (e) => {
        const file = e.target.files[0];

        if (file) {

            const reader = new FileReader();

            reader.onloadend = () => {
                setImage(reader.result);
                setCroppedImage(null);
                setAllowUpload(true); // Allow upload when a new image is selected
                setShowRecropBtn(false);
                alert('Image is Upload Success', 'success');
            };

            reader.readAsDataURL(file);
        }
    };

    const handleCrop = () => {

        if (cropperRef.current && cropperRef.current.cropper) {

            const croppedCanvas = cropperRef.current.cropper.getCroppedCanvas();

            if (croppedCanvas) {

                const croppedImageBase64 = croppedCanvas.toDataURL();

                setCroppedImage(croppedImageBase64);
                setAllowUpload(true);
                setShowRecropBtn(true);
                alert('Image is cropped', 'success');

            }
        }
    };

    const handleReset = () => {

        setImage(null);
        setCroppedImage(null);
        setAllowUpload(true);
        setShowRecropBtn(false);

        // Clear the existing cropper canvas
        if (cropperRef.current && cropperRef.current.cropper) {
            cropperRef.current.cropper.destroy();
        }

        // Clear the file input value to allow selecting the same file again
        if (inputRef.current) {
            inputRef.current.value = '';
        }
    };

    const handleRecrop = () => {
        setCroppedImage(null);
        setAllowUpload(true);
    };


    return (
        <div className='' onClick={handleImgClick}>
            <div className="card">
                {image && !croppedImage && (
                    <Cropper ref={(cropper) => (cropperRef.current = cropper)} src={image} guides={true} />
                )}

                {croppedImage && (
                    <div>
                        <img className='img-fluid' src={croppedImage} id='croppedimgInput' alt='Cropped' />
                    </div>
                )}

                {!image && !croppedImage && (
                    <img src={NatureImg} className='img-fluid card-img-top' alt='Sorry something is missing' />
                )}

                <div className='card-body bg-dark'>
                    <input className='form-control d-none' onChange={handleImgChange} ref={inputRef} type='file' />
                    {image ? (
                        <>
                            <h5 className='fa-fade text-light fw-bold text-center'>Adjust and Crop Your Image</h5>
                            <div className='d-flex justify-content-around'>
                                <button className='btn btn-success btn-sm' onClick={handleCrop} disabled={!allowUpload}>
                                    Crop Image
                                </button>
                                <button className='btn btn-danger btn-sm' onClick={handleReset}>
                                    Reset
                                </button>
                                {showRecropBtn && (
                                    <button className='btn btn-primary btn-sm' onClick={handleRecrop}>
                                        Recrop
                                    </button>
                                )}

                            </div>
                        </>
                    ) : (
                        <h5 className='fa-fade text-light fw-bold text-center'>Click To Upload Your Image</h5>
                    )}
                </div>
            </div>
        </div>
    );
}
