import React, { useState } from 'react';

import InputsIcon from './InputsIcon';
import GetInputFromUser from './GetInputFromUser';

import Logo from "../../Common/Logo/Logo.png";

export default function InputsPage() {

    const [image, setImage] = useState(false);
    const [video, setVideo] = useState(false);
    const [audio, setAudio] = useState(false);

    const getImage = () => {
        setImage(true);
    }
    const getVideo = () => {
        setVideo(true);
    }
    const getAudio = () => {
        setAudio(true);
    }

    return (
        <>
            <div className='g-0 ' id='input_page_top_logo'>
                <a href="/"><img className='img-fluid mt-md-3 mt-5 ms-3 ' width={"190px"} src={Logo} alt="Something Not found" /></a>
            </div>

            <div className="row mt-5 m-0 gx-3 d-flex justify-content-center mb-2 px-3 ">

                <div className="col-md-1 rounded d-none d-md-block pt-3 pb-4 inputcontainerBG rounded-pill-md " >
                    <InputsIcon getAudio={getAudio} getVideo={getVideo} getImage={getImage} />
                </div>

                <div className="col-11">
                    <GetInputFromUser video={video} audio={audio} image={image} />
                </div>

            </div>
        </>

    )
}
