import React, { createContext, useState } from 'react';

import Alert from './Common/Alert/Alert';
import MainPage from './Pages/MainPage';

export const sendAlert = createContext(null);

function App() {

  const [alert, setAlert] = useState(false);

  const showAlert = (message, type) => {

    setAlert({
      msg: message,
      type: type
    });

    setTimeout(() => {
      setAlert(false);
    }, 4000);

  }

  return (
    <>
      <sendAlert.Provider value={showAlert}>
        <Alert alert={alert} />
        <MainPage />
      </sendAlert.Provider>
    </>

  );
}

export default App;
