import React, { useRef, useState } from 'react';

// import VideoTrimmer from 'react-video-trimmer';

import DummyVideo from '../../../Common/Videos/DummyVideo.mp4';

export default function GetVideoInput() {
   
    const [videos, setVideo] = useState("");
    const inputRef = useRef(null);

    const handleImgClick = () => {
        inputRef.current.click();
    };

    const handleVideoChange = (e) => {
        const file = e.target.files[0];
        setVideo(file);
        if (!videos) {
            inputRef.current.click();
        }
    };

    return (
        <div className='' onClick={handleImgClick}>
            <div className="card">
                {videos ? (
                    <video
                        src={URL.createObjectURL(videos)}
                        className="object-fit-cover"
                        controls
                        muted
                    />
                ) : (
                    <video
                        src={DummyVideo}
                        className="object-fit-cover object-fit-fill"
                        autoPlay
                        loop
                        muted
                    ></video>
                )}

                <div className="card-body bg-dark">
                    <input
                        className="form-control d-none"
                        onChange={handleVideoChange}
                        ref={inputRef}
                        type="file"
                    />

                    <h5 className="fa-fade text-light fw-bold text-center">
                        Click To Upload Your File
                    </h5>
                </div>
            </div>
        </div>
    );
}
