import React from 'react';

import getaudio from '../../Common/icons/getaudio.png';
import getvideo from '../../Common/icons/videoinput.png';
import getimgs from '../../Common/icons/getimgs.png';

export default function InputsIcon(props) {

const handleImgs = () => {
    props.getImage();
}
const handleVideo = () => {
    props.getVideo();
}
const handleAudio = () => {
    props.getAudio();
}

    return (

        <div className='d-flex flex-md-column mt-4 ps-3 justify-content-evenly'>

            <img className='img-fluid input-icon mt-2' onClick={handleImgs} src={getimgs} alt="Icon" />
            <img className='img-fluid input-icon mt-md-5' onClick={handleAudio} src={getaudio} alt="Icon" />
            <img className='img-fluid input-icon mt-md-5' onClick={handleVideo} src={getvideo} alt="Icon" />

        </div>

    )
}
