import React, { useRef, useState, useEffect } from 'react';
import WaveSurfer from 'wavesurfer.js';

const GetVoice = () => {
  const audioRef = useRef(null);
  const wavesurferRef = useRef(null);
  const mediaRecorderRef = useRef(null);
  const [isRecording, setIsRecording] = useState(false);
  const [recordingTime, setRecordingTime] = useState(0);
  const [cropStart, setCropStart] = useState(0);
  const [cropEnd, setCropEnd] = useState(0);

  useEffect(() => {
    wavesurferRef.current = WaveSurfer.create({
      container: '#waveform', // Make sure this matches the ID in your HTML
      waveColor: 'violet',
      progressColor: 'purple',
      cursorWidth: 1,
      height: 100,
    });

    return () => {
      if (wavesurferRef.current) {
        wavesurferRef.current.destroy();
      }
    };
  }, []);

  useEffect(() => {
    let timer;
    if (isRecording) {
      timer = setInterval(() => {
        setRecordingTime((prevTime) => prevTime + 1);
      }, 1000);
    }
    return () => {
      clearInterval(timer);
    };
  }, [isRecording]);

  const handleStartRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });

      const mediaRecorder = new MediaRecorder(stream);
      mediaRecorderRef.current = mediaRecorder;

      const chunks = [];

      mediaRecorder.ondataavailable = (event) => {
        if (event.data.size > 0) {
          chunks.push(event.data);
        }
      };

      mediaRecorder.onstop = () => {
        const blob = new Blob(chunks, { type: 'audio/wav' });
        const audioUrl = URL.createObjectURL(blob);

        wavesurferRef.current.load(audioUrl);

        if (audioRef.current) {
          audioRef.current.src = audioUrl;
        }

        setCropStart(0);
        setCropEnd(wavesurferRef.current.getDuration());
        setRecordingTime(0); // Reset recording time after recording is done
      };

      mediaRecorder.start();
      setIsRecording(true);
    } catch (error) {
      console.error('Error accessing microphone:', error);
    }
  };

  const handleStopRecording = () => {
    if (mediaRecorderRef.current) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);
    }
  };

  const handleCropChange = (event) => {
    const { name, value } = event.target;

    if (name === 'cropStart') {
      setCropStart(Number(value));
    } else if (name === 'cropEnd') {
      setCropEnd(Number(value));
    }
  };

  const handleCropAudio = () => {
    wavesurferRef.current.seekTo(cropStart / wavesurferRef.current.getDuration(), cropEnd / wavesurferRef.current.getDuration());
    wavesurferRef.current.play();
  };

  return (
    <div className="card bg-dark border-light">
      <div className='d-flex p-2 flex-column justify-content-evenly'>
        <div className='mb-2'>
          {!isRecording ? (
            <button
              className='btn py-2 btn-sm btn-success border-0 col-12'
              style={{ backgroundColor: isRecording ? '#dc3545' : '#28A745', cursor: 'pointer' }}
              onClick={handleStartRecording}
            >
              Start Recording
            </button>
          ) : (
            <button
              className='btn py-2 btn-sm btn-danger border-0 col-12 '
              style={{ backgroundColor: '#dc3545', cursor: 'pointer' }}
              onClick={handleStopRecording}
            >
              Stop Recording {recordingTime} seconds
            </button>
          )}
        </div>

        {!isRecording && (
          <>
            <div className='mb-1'>
              <label className='d-block mb-2 text-light' htmlFor="cropStart">
                Crop Start Time: {cropStart} seconds
              </label>
              <input
                type="range"
                name="cropStart"
                className="form-range"
                id="cropStart"
                min="0"
                max={cropEnd}
                value={cropStart}
                onChange={handleCropChange}
              />

            </div>

            <div className='mb-2'>
              <label className='mb-2 d-bock text-light' htmlFor="cropEnd">
                Crop End Time: {cropEnd} seconds
              </label>
              <input
                type="range"
                name="cropEnd"
                className="form-range"
                id="cropEnd"
                min={cropEnd}
                max={wavesurferRef.current?.getDuration() || 0}
                value={cropEnd}
                onChange={handleCropChange}
              />

            </div>

            <button
              className='btn py-2 btn-sm btn-success border-0'
              style={{ cursor: 'pointer' }}
              onClick={handleCropAudio}
            >
              Crop Audio
            </button>

            <div className='d-none' id="waveform"></div>

            <audio
              className='form-control'
              controls
              style={{ marginTop: '10px' }}
              ref={(audio) => (audioRef.current = audio)}
            ></audio>
          </>
        )}
      </div>
    </div>
  );
};

export default GetVoice;
